import { useReducer, createContext, useContext, useEffect } from "react";
import { todoApi } from "../Api/TodoApi.js";

const TodoContext = createContext();
const TodoDispatchContext = createContext ();

const TodoReducre = (todos, { type, todo }) => {
  switch(type){
    case "todo/init":
      return [ ...todo ];

    case "todo/add":
      return [ ...todos, todo ];

    case "todo/delete":
      return todos.filter(_todo => {
        return _todo.id !== todo.id;
      });
    
    case "todo/update":
      return todos.map(_todo => {
        return _todo.id === todo.id
          ? { ..._todo, ...todo }
          : { ..._todo };
      });

    default:
      return todos;
  }
};

export const TodoProvider = ({ children }) => {

  const [ todos, dispatch ] = useReducer(TodoReducre, []);

  useEffect(_ => {
    todoApi.getAll()
    .then(response => {
      dispatch({ type: "todo/init", todo: response });
    });
  }, []);

  return (
    <TodoContext.Provider value={ todos }>
      <TodoDispatchContext.Provider value={ dispatch }>
        { children }
      </TodoDispatchContext.Provider>
    </TodoContext.Provider>
  );
};

export const useTodo = _ => useContext(TodoContext);
export const useTodoDispatch = _ => useContext(TodoDispatchContext);