import { socket } from "../socket.js";
import { useTodoDispatch } from "../context/TodoContext.js";

export const Actions = _ => {

  const dispatch = useTodoDispatch();

  socket.on("todo/add", newTodo => {
    dispatch({ type: "todo/add", todo: newTodo });
  });
  
  socket.on("todo/update", todo => {
    dispatch({ type: "todo/update", todo });
  });
  
  socket.on("todo/delete", todo => {
    dispatch({ type: "todo/delete", todo });
  });
};