import axios from "axios";

const ENDPOINT_URL = "http://192.168.11.6:3000/todos";
// const ENDPOINT_URL = "https://nodejs-mysql-api-server-89c2f3154f98.herokuapp.com/todos";

export const todoApi = {
  async getAll(){
    const result = await axios.get(ENDPOINT_URL);
    return result.data;
  },
  async post(todo){
    const result = await axios.post(ENDPOINT_URL, todo);
    return result.data;
  },
  async update(todo){
    const result = await axios.patch(ENDPOINT_URL + `/` + todo.id, todo);
    return result.data;
  },
  async delete(todo){
    const result = await axios.delete(ENDPOINT_URL + `/` + todo.id);
    return result.data;
  }
}; 