import { useState } from "react";
import { todoApi } from "../Api/TodoApi.js";
import { socket } from "../socket.js";

export const Form = _ => {

  const [ enteredTodo, setEnteredTodo ] = useState("");

  const addTodo = e => {
    e.preventDefault();
    const newTodo = {
      id: Math.floor(Math.random() * 1e5),
      content: enteredTodo,
      editing: false
    };
    todoApi.post(newTodo)
    .then(response => {
      const newTodo = { ...response[0], editing: false };
      socket.emit("todo/add", newTodo);
    });
    setEnteredTodo("");
  };

  return (
    <div className="add-form">
      <form
        onSubmit={ addTodo }
      >
        <input
          type="text"
          value={ enteredTodo }
          onChange={ e => setEnteredTodo(e.target.value) }
        />
        <button
          type="submit"
          style={{ marginLeft: "5px" }}
        >add</button>
      </form>
    </div>
  );
};