import { useTodo } from "../context/TodoContext.js";
import { Suspense, lazy } from "react";

const LazyComponent = lazy(_ => import("./Item.js"));

export const List = _ => {

  const todos = useTodo();

  return (
    <div className="todos">
      {
        todos.map((todo, index) => (
          <Suspense
            fallback={
              <div
                style={{
                  padding: "20px",
                  color: "#fff",
                  fontWeight: "bold",
                  fontSize: ".8em"
                }}
              >
                Loading Component .....
              </div>
            }
          >
            <LazyComponent key={ index } todo={ todo } />
          </Suspense>
        ))
      }
    </div>
  );
};